.base {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../../img/iniverse.jpg");
  height: 100%;
  width: 100%;
  color: var(--base01);
  margin-top: 0px;
  margin-bottom: 0px;
}

.description {
  text-align: center;
}

.description :global .Cursor :local {
  display: inline-block;
  line-height: 0.3;
  animation: blink 1s linear infinite;
  font-size: 29px;
  color: var(--base03);
}

.description :global .Cursor::after :local {
  content: "\1F9D8";
  font-size: 1em;
}

.description > div {
  display: inline-block;
  line-height: 1.95;
}

.description h1 {
  font-weight: 800;
  font-size: 29px;
  display: inline-block;
  color: var(--base03);
  margin: 0;
}

.description h2 {
  font-weight: 800;
  font-size: 18px;
  display: inline-block;
  color: var(--base03);
  margin: 0;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
